* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.card {
  background-image: url("imac.202c52ff.jpeg");
  background-position: 19px 32px;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__link {
  color: #060b35;
  padding: 174px 0 4px;
  text-decoration: none;
  display: block;
}

.card__link, .card__price {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__text--code {
  padding-bottom: 16px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.card__text--review {
  color: #060b35;
  text-decoration: none;
}

.card__wrap {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
}

.card__numb {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.buy {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
